import React, { useState } from 'react';
import { Link } from "gatsby";
import { GatsbyImage, getImage } from "gatsby-plugin-image";
import { graphql, useStaticQuery } from "gatsby";
import styled from "styled-components";
import ModalContact from '../../components/ModalContact';
import autocraneTexts from '../../shortinfo/autocranes';
import modelNames from '../../modelNames/autocranes';
import ModelCard from '../../components/ModelCard';
import Header from '../../components/Header';

const autocranes = [
  "ZMC251C", "ZMC601W1", "ZTC250V", "ZTC300V", "ZTC600V5521T", "ZTC600V5522C", "ZTC800V", "ZTC800V5525C",
  "ZTC1000V", "ZTC1500H", "ZAT2000V"
];

const AutocranesPage = () => {
  const [isModalOpen, setIsModalOpen] = useState(false);
  const [selectedModel, setSelectedModel] = useState(null);
  const [selectedModelText, setSelectedModelText] = useState("");
  
  // Обработчик клика по карточке модели
  const handleCardClick = (model) => {
    setIsModalOpen(true);
    setSelectedModel(model);
    // Используем modelNames для получения удобочитаемого названия
    setSelectedModelText(modelNames[model] || model);
  };
  // Функция закрытия модального окна
  const closeModal = () => {
    setIsModalOpen(false);
    setSelectedModel(null);
    setSelectedModelText("");
  };

  const images = useStaticQuery(graphql`
    query {
      allFile(filter: { relativeDirectory: { eq: "autocranes" } }) {
        edges {
          node {
            name
            childImageSharp {
              gatsbyImageData(width: 300, layout: CONSTRAINED)
            }
          }
        }
      }
    }
  `);

  // Функция для получения изображения по имени модели
  const getModelImage = (model) => {
    const imageNode = images.allFile.edges.find(edge => edge.node.name === model);
    return imageNode ? getImage(imageNode.node.childImageSharp) : null;
  };

  return (
    <Layout>
      <Header centerText="Автокраны" />

      <Container>
        <Grid>
          {autocranes.map((model) => (
            <ModelCard 
              key={model}
              modelKey={model}
              imageData={getModelImage(model)}
              onClick={() => handleCardClick(model)}
            />
          ))}
        </Grid>
      </Container>

      {/* Передаём в модальное окно как компонент модель из autocraneTexts */}
      <ModalContact
        isOpen={isModalOpen}
        onClose={closeModal}
        ModelComponent={autocraneTexts[selectedModel]}
      >
        {/* Если нужно, можно добавить выбранный текст прямо сюда */}
        {selectedModelText && <Subtitle>{selectedModelText}</Subtitle>}
      </ModalContact>

      <Footer>
        <p>&copy; {new Date().getFullYear()} Зумлион Индустри. 25 лет на рынке в мире</p>
      </Footer>
    </Layout>
  );
};

export default AutocranesPage;

export const Head = () => (
  <>
    <title>Zoomlion</title>
    <link rel="icon" type="image/x-icon" href="/favicon.png" />
  </>
);
/* Стилизация компонентов */

const Layout = styled.div`
  display: flex;
  min-height: 100vh;
  flex-direction: column;
`;

const Container = styled.div`
  padding: 20px;
  max-width: 1200px;
  width: 100%;
  margin: 0 auto;
  flex: 1 0 auto;
`;

const Grid = styled.div`
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
  gap: 16px 8px; /* 16px для вертикального отступа и 8px для горизонтального */
  justify-items: center;
  padding: 20px;

  @media (min-width: 768px) {
    justify-content: space-between;
  }
`;

const Footer = styled.footer`
  text-align: center;
  padding: 20px;
  background: #333;
  color: white;
  margin-top: auto;
`;

// Дополнительный стиль для вывода удобочитаемого названия выбранной модели
const Subtitle = styled.p`
  font-size: 18px;
  font-weight: bold;
  text-align: center;
  margin-top: 1rem;
`;