// Header.js
import React from 'react';
import styled from 'styled-components';
import { Link } from 'gatsby';

const HeaderContainer = styled.header`
  padding: 20px;
  background: #eee;
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;

  /* Для экранов от 768px — располагаем три блока в строку */
  @media (min-width: 768px) {
    flex-direction: row;
    justify-content: space-between;
    text-align: left;
  }
`;

/* Левый блок с логотипом */
const LeftContainer = styled.div`
  margin-bottom: 10px;
  /* Анимация появления */
  animation: fadeIn 1s ease-in-out;
  @media (min-width: 768px) {
    margin-bottom: 0;
  }
`;

/* Центральный блок с динамическим текстом */
const CenterContainer = styled.div`
  font-size: 2rem; /* Меньший размер для мобильных устройств */
  color: #333;
  margin-bottom: 10px;

  @media (min-width: 768px) {
    font-size: 4rem; /* Увеличенный размер для экранов от 768px */
    margin-bottom: 0;
    flex-grow: 1;
    text-align: center;
    align-self: center;
  }
`;

/* Правый блок с контактной информацией */
const RightContainer = styled.div`
  @media (min-width: 768px) {
    display: flex;
    align-items: center;
  }
`;

const Logo = styled.h1`
  font-size: 4rem;
  color: #006f3d; /* Фирменный зелёный */
  margin: 0;

  a {
    text-decoration: none;
    color: inherit;
  }
`;

const ContactInfo = styled.div`
  text-align: center;

  @media (min-width: 768px) {
    text-align: right;
  }
`;

const City = styled.p`
  margin: 0;
  font-size: 1.2rem;
  color: #333;
`;

const Phone = styled.a`
  display: block;
  margin: 5px 0;
  font-size: 1.2rem;
  color: #333;
  text-decoration: none;
  transition: color 0.3s ease;

  &:hover {
    color: #006f3d;
  }
`;

const TelegramLink = styled.a`
  display: inline-flex;
  align-items: center;
  font-size: 1.2rem;
  color: #333;
  text-decoration: none;
  transition: color 0.3s ease;

  &:hover {
    color: #006f3d;
  }
`;

const TelegramIcon = styled.img`
  width: 32px;
  height: 32px;
  margin-left: 8px;
  transform: translateY(12px); /* смещаем иконку вниз для лучшего выравнивания */
`;

const Header = ({ centerText }) => {
  return (
    <HeaderContainer>
      <LeftContainer>
        <Logo>
          <Link to="/">Zoomlion</Link>
        </Logo>
      </LeftContainer>
      {centerText && (
        <CenterContainer>
          {centerText}
        </CenterContainer>
      )}
      <RightContainer>
        <ContactInfo>
          <City>г. Новосибирск</City>
          <Phone href="tel:+7-923-708-22-54">+7-923-708-22-54</Phone>
          <TelegramLink
            href="https://t.me/gmitry"
            target="_blank"
            rel="noopener noreferrer"
          >
            Telegram
            <TelegramIcon
              src="https://upload.wikimedia.org/wikipedia/commons/thumb/8/82/Telegram_logo.svg/512px-Telegram_logo.svg.png"
              alt="Telegram logo"
            />
          </TelegramLink>
        </ContactInfo>
      </RightContainer>
    </HeaderContainer>
  );
};

export default Header;
